.information{
  width: 85%;
  display: flex;
  margin-left: 155px;
  position: relative;
  top: 100px;
}
.description {
  width: 100%;
  margin-right: 50px;
}


/* ************* Media query ************ */
@media screen and (min-width: 360px) and (max-width: 767px) {
  .information{
    width: 92%;
    margin-left: 7px;
    top: 260px;
    display: flex;
    flex-direction: column;
  }
}

@media screen and (min-width: 768px) and (max-width: 1450px) {
 .information{
  width: 80%;
  margin: auto ;
 }
 .description{
  margin-right: 1%;
  padding-left: 2%;
 }
}